// 这是一个父类，所有的公用方法和公用变量都写在这里面		
class ParentFn{
	constructor(){
		//构造器，属性写在这里面
		this.host = 'http://61.144.244.75:16680/';
	};
	ajax(url, data,that,success,method,loadingIcon) {
		var _this = this,loading;
		if(!loadingIcon) {
			loading = that.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.5)'
			});
		};
		console.log(JSON.stringify(data));
		if(method) {
			method = method;
		} else {
			method = 'post';
		};
		that.$ajax({
			method: method,
			url: this.host + url,
			data: data,
			headers: {
				'Content-Type': 'application/json'
			}
		})
		.then((res)=>{
			if(!loadingIcon){
				loading.close();
			}
			success(res.data);
			console.log(res.data);
		})
		.catch((err)=>{
			if(!loadingIcon){
				loading.close();
			}
			var error = err.response.data;
			success(error);
			console.log(error);
			if(error.error.code == 500){
				that.$message.error(error.error.message);
			}
		});
	};
	//用了做elementui表单校验的
	//不能为空
	noIsEmpty(msg){
		return	(rule,value,callback)=>{
			if(typeof value == 'object') {
				var value = value.model;
			};
			if(/^\S+$/.test(value) == false || !value){
				callback(new Error(msg));
			}else{
				callback();
			}
		}
	};
	//树菜单增删改查
	//递归判断增加当前数组有没有同名的
	testNameIsRepeat(arr,menuName){
		this.boole = false;
		for(var i in arr) {
			//这个数组里面有相同的名称为true
			if(arr[i].menuName == menuName) {
				this.boole = true;
				break;
			}
		}
	};
	//递归判断添加菜单同级下有没有重复的名称
	testTreeName(tree,menuName,curNodeId){
		for(var i in tree) {
			//当树的id与选中id相同证明找到同级的数组
			if(tree[i].id == curNodeId) {
				//child的长度大于0才比较
				if(tree[i].child && tree[i].child.length > 0) {
					this.testNameIsRepeat(tree[i].child,menuName);
				} else {
					this.boole = false;
				}
			} else {
				if(tree[i].child && tree[i].child.length) {
					this.testTreeName(tree[i].child,menuName,curNodeId);
				}
			}
		}
	};
	//判断修改当前数组有没有同名的
	testReviseNameIsRepeat(arr,menuName,curNodeId){
		this.boole = false;
		for(var i in arr) {
			//这个数组里面有相同的名称为true
			if(arr[i].menuName == menuName && arr[i].id != curNodeId) {
				this.boole = true;
				break;
			}
		}
	};
	//递归判断修改有没有重复的名称
	testReiveseTreeName(tree,menuName,curNodeId){
		for(var i in tree) {
			//找到当前id的数组 
			if(tree[i].id == curNodeId) {
				this.testReviseNameIsRepeat(tree,menuName,curNodeId);
			} else {
				if(tree[i].child && tree[i].child.length > 0) {
					this.testReiveseTreeName(tree[i].child,menuName,curNodeId);
				}
			}
		}
	};
	//递归修改参数
	reviseMenuTree(obj,id,curObj){
		//obj为修改前的树，修改的节点id，curObj修改之后返回的对象
		for(var i in obj) {
			//id相同的时候把返回的值除了child之外赋值给修改前的
			if(obj[i].id == id) {
				for(var key in obj[i]) {
					if(key != 'child') {
						obj[i][key] = curObj[key];
					}
				}
			} else {
				if(obj[i].child && obj[i].child.length) {
					this.reviseMenuTree(obj[i].child,id,curObj);
				}
			}
		}
		return obj;
	};
	//递归判断删除的节点有没有子节点
	nodeIsChild(tree,curId){
		this.boole = false;
		for(var i in tree) {
			//名称相同且id不相同，证明有重复
			if(tree[i].id == curId) {
				if(tree[i].child && tree[i].child.length) {
					this.boole = true;
					return;
				}
			} else {
				if(tree[i].child && tree[i].child.length) {
					this.nodeIsChild(tree[i].child,curId);
				}
			}
		}
	};
	//给树的每一项添加一个属性
	setTreeChecked(list,select){
		for(var i = 0;i < list.length;i++) {
			list[i][select] = false;
			if(list[i].child && list[i].child.length > 0) {
				this.setTreeChecked(list[i].child,select);
			}
		}
		return list;
	};
	//用树的一个参数与一个值indexOf比较来改变一个值的true
	setTreeSelect(list,id,select,value){
		for(var i = 0;i < list.length;i++) {
			if(list[i][id].indexOf(value) > -1) {
				list[i][select] = true;
			} else {
				list[i][select] = false;
			}
			if(list[i].child && list[i].child.length > 0) {
				this.setTreeSelect(list[i].child,id,select,value);
			}
		}
		return list;
	};
}
export default{
	ParentFn: ParentFn
}


